:local {
    .cardWrapper {
        width: 100%;
        height: 100%;
        overflow: hidden;
        border-radius: 15px;
    }

    .card {
        height: 100%;
        position: relative;
        padding-top: 40px;
        border-radius: 15px;
        overflow: hidden;
        z-index: 1;
    }

    .icon {
        top: -5px;
        right: 15px;
        position: absolute;
        cursor: pointer;
        z-index: -1;
    }

    .cardInfo {
        display: flex;
        flex-direction: column;
        z-index: 2;
        height: 100%;

        padding: 25px;
        border-radius: 15px 15px 0 0;
        cursor: pointer;

        color: white;
        background-color: hsl(235, 46%, 20%);
    }

    .cardInfo:hover {
        background-color: hsl(235, 46%, 30%);
    }

    .cardHeader {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .cardHeaderTitle {
        font-weight: 500;
    }

    .cardHeaderAdditional {
        height: 5px;
        width: 21px;
        cursor: pointer;
    }

    .cardHeader {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .cardBody {
        display: flex;
        flex-direction: column;

        padding-top: 25px;
    }

    .cardBodyTime {
        font-size: 48px;
        font-weight: 300;
    }

    .cardBodyPreviousTime {
        font-size: 14px;
        padding-top: 10px;
        color: hsl(236, 100%, 87%);
    }

    @media only screen and (max-width: 375px) {
        .cardBody {
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            padding-top: 5px;
        }

        .cardBodyTime {
            font-size: 32px;
        }

        .cardBodyPreviousTime {
            padding-top: 0;
        }
    }

    .work {
        background-color: hsl(15, 100%, 70%);
    }

    .play {
        background-color: hsl(195, 74%, 62%);
    }

    .study {
        background-color: hsl(348, 100%, 68%);
    }

    .exercise {
        background-color: hsl(145, 58%, 55%);
    }

    .social {
        background-color: hsl(264, 64%, 52%);
    }

    .self-care {
        background-color: hsl(43, 84%, 65%);
    }
}
